h1,
h2,
h3,
h4,
h5,
li,
a {
	font-family: 'Open Sans', sans-serif;
}

p {
	font-family: 'Roboto', sans-serif;
}

h5,
h3 {
	font-weight: 600;
}

.btn-rounded {
	border-radius: 28px;
	color: white;
	background-color: rgb(14, 5, 7);
	border: none;
	padding-right: 20px;
	padding-left: 20px;
}

a:hover {
	text-decoration: none;
	color: #1a0f38;
}

* {
	transition: .2s all ease-in-out;
}

html {
	scroll-behavior: smooth;
}
